import React, { Fragment } from 'react';
import Signup from '../forms/Signup';
import Login from '../forms/Login';
import ForgotPassword from '../forms/ForgotPassword';

import Popup from "reactjs-popup";

class MenuLink extends React.Component {
    constructor(props){
		super(props);
		this.state = { login: false, forgotpassword : false, signup: false };
		this.openCloseLogin = this.openCloseLogin.bind(this);
		this.openCloseSignup = this.openCloseSignup.bind(this);
		this.openForgotPassword = this.openForgotPassword.bind(this);
		this.closeForgotPassword = this.closeForgotPassword.bind(this);
	}	

	openCloseLogin() {
		this.setState({ login: !this.state.login });
	}

	openCloseSignup() {
		this.setState({ signup: !this.state.signup });
	}

	openForgotPassword() {
		this.setState({ login: !this.state.login });
		this.setState({ forgotpassword: !this.state.forgotpassword });
	}

	closeForgotPassword() {
		this.setState({ forgotpassword: !this.state.forgotpassword });
	}
	render(){
		return(
			<Fragment>
                <ul className="links">
                    <li><a href={"#"} className="link">Pricing</a></li>
                    <li><a href="#" className="link">Articals</a></li>
                    <li><a href="#" className="link">Free Tools</a></li>
                    <li>
                        <a className="link" onClick={this.openCloseLogin}>Login</a>
                        <Popup modal open={this.state.login} closeOnDocumentClick={false} closeOnEscape={false}>
                            <div className="popup-container">
                                <span className="close-on-popup" onClick={this.openCloseLogin}><i className="fa fa-times-circle"></i></span>
                                <Login>
                                    <a className="forgot-password-link" onClick={this.openForgotPassword}>Forgot your password?</a>
                                </Login>	
                            </div>
                        </Popup>
                        
                        <Popup modal open={this.state.forgotpassword} closeOnDocumentClick={false} closeOnEscape={false}>
                            <div className="popup-container">
                                <span className="close-on-popup" onClick={this.closeForgotPassword}><i className="fa fa-times-circle"></i></span>
                                <ForgotPassword/>
                            </div>
                        </Popup>
                    </li>
                    <li>
                        <button className="signup-btn" onClick={this.openCloseSignup}>Sign Up Free</button>
                        <Popup modal open={this.state.signup} closeOnDocumentClick={false} closeOnEscape={false}>
                            <div className="popup-container">
                                <span className="close-on-popup" onClick={this.openCloseSignup}><i className="fa fa-times-circle"></i></span>
                                <Signup/>
                            </div>
                        </Popup>
                    </li>
                </ul>
			</Fragment>
		);
	}
}

export default MenuLink;  
