import React, { Fragment } from 'react';
import './style.css';
import '../comman.css';
import Menu from './MenuLinks';

class Header extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			res_menu : false,
		}
		this.showMenuMobile = this.showMenuMobile.bind(this);
	}

	showMenuMobile(){
		console.log("te");
		this.setState({res_menu:!this.state.res_menu});
	}

	render(){
		return(
			<Fragment>
				<div className="nav">
					<div className="nav-logo"><h3><i className="fa fa-chart-line"></i> SEO Health</h3></div>
					<div className="nav-links">
						<div className="res-links">
							<button onClick={this.showMenuMobile}>
								<i className="fa fa-bars"></i>
							</button>
						</div>
						<div className="des-link">
							<Menu/>
						</div>
					</div>
					<div className={(this.state.res_menu)?"nav-res-links nav-show":"nav-res-links nav-hide"}>
						<Menu/>
						<span onClick={this.showMenuMobile}><i className="fa fa-times-circle fa-2x"></i></span>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default Header;  
